import React from "react";
import styled from "styled-components";

const StyledButton = styled.button`
  background: #e83e8c;
  border-radius: 8px;
  font-family: Mulish;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  padding: 15px 25px;
  border: none;
  cursor: pointer;
`;

export default function Button(props) {
  return <StyledButton {...props} />;
}
