import React from "react";
import styled from "styled-components";
import Container from "../../../Components/container";
import BlurCircle from "../../../Assets/images/BlurCircle.png";
import BannerImg from "../../../Assets/images/Banner.png";
import IconLock from "../../../Assets/images/icon-lock.png";
import IconWallet from "../../../Assets/images/icon-wallet.png";
import IconSun from "../../../Assets/images/icon-sun.png";
import DownloadGoogle from "../../../Assets/images/download-google.png";
import DownloadIos from "../../../Assets/images/download-ios.png";
import DownloadChrome from "../../../Assets/images/download-chrome.png";
const Wrapper = styled.div`
  height: calc(100vh - 120px);
  background: url(${BlurCircle}), url(${BannerImg});
  background-size: contain;
  background-position: top left, center right;
  background-repeat: no-repeat;
  & > div {
    height: 100%;
  }
`;
const Content = styled.div`
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  h1 {
    font-family: "Provicali";
    font-size: 110px;
    line-height: 94px;
    text-transform: uppercase;
    color: #e83e8c;
    margin-bottom: 50px;
    span {
      color: #fff;
    }
}
p {
  font-family: Mulish;
  font-size: 24px;
  line-height: 30px;
  color: #ffd6e9;
  margin-bottom: 24px;
}
`;
const Details = styled.div`
  display: flex;
  gap: 6px;
  margin-bottom: 24px;
`;
const DetailsItem = styled.div`
  border: 1px solid #ffd6e9;
  border-radius: 15px;
  padding: 12px 15px;
  display: flex;
  align-items: center;
  gap: 9px;
  font-family: Mulish;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: #ffd6e9;
`;
const Flex = styled.div`
    display: flex;
    gap: 16px;
`;
export default function Banner() {
  return (
    <Wrapper>
      <Container>
        <Content>
          <h1>
            Centpay <br />
            <span> Wallet</span>
          </h1>
          <Details>
            <DetailsItem>
              <img src={IconLock} alt="icon-lock" />
              Secure
            </DetailsItem>
            <DetailsItem>
              <img src={IconWallet} alt="icon-wallet" />
              NFT Assets
            </DetailsItem>
            <DetailsItem>
              <img src={IconSun} alt="icon-sun" />
              Decentralized
            </DetailsItem>
          </Details>
          <p>Trusted and Secure Defi Wallet in Meteverse</p>
          <Flex>
            <img src={DownloadGoogle} alt="download-google" />
            <img src={DownloadIos} alt="download-ios" />
            <img src={DownloadChrome} alt="download-chrome" />
          </Flex>
        </Content>
      </Container>
    </Wrapper>
  );
}
