import React from "react";
import styled from "styled-components";
import Container from "../../../Components/container";
import ChartImg from "../../../Assets/images/chart.png";
import Blur2 from "../../../Assets/images/Blur2.png";
const Wrapper = styled.div`
  padding-bottom: 100px;
  background-image: url(${Blur2});
    background-repeat: no-repeat;
    background-size: contain;
    background-position: bottom right;
  h2 {
    font-family: Roboto;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    text-transform: uppercase;
    color: #e83e8c;
    margin-bottom: 12px;
  }
  h1 {
    font-family: Mulish;
    font-weight: bold;
    font-size: 36px;
    line-height: 44px;
    text-align: center;
    color: #ffffff;
    margin-bottom: 20px;
  }
  p {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    color: #a6a6a6;
    margin-bottom: 56px;
  }
`;
const Content = styled.div`
  display: flex;
  gap: 90px;
`;
const ContentItem = styled.div`
  display: flex;
  gap: 30px;
  margin-bottom: 25px;
  & > div:first-child {
    border-radius: 8px;
    width: 65px;
    height: 38px;
  }
  ul {
    width: calc(100% - 95px);
    li {
      list-style: disc outside none;
      font-family: Roboto;
      font-size: 18px;
      line-height: 21px;
      font-weight: 400;
      color: #a6a6a6;
      span {
        font-weight: 500;
        color: #ffffff;
      }
    }
  }
`;
export default function Tokenomics() {
  return (
    <Wrapper>
      <Container>
        <h2>Tokenomics</h2>
        <h1>Tokenomics</h1>
        <p>CENT is the token that governs the entire Centbox ecosystem</p>
        <Content>
          <div>
            <ContentItem>
              <div
                style={{
                  background: "#CA0861",
                }}
              ></div>
              <ul>
                <li>
                  <span>Privatesale 3%:</span> Unlock 30%, unlock 30% after 6
                  months, unlock 40% after 1 year.
                </li>
              </ul>
            </ContentItem>
            <ContentItem>
              <div
                style={{
                  background: "#FC157F",
                }}
              ></div>
              <ul>
                <li>
                  <span>Publicsale 30%:</span> Selling in 6 months price
                  increments, starting price 0.02, increasing 1% every day for
                  180 days, before listing floor, listing floor starting price
                  at least 0.1.
                </li>
              </ul>
            </ContentItem>
            <ContentItem>
              <div
                style={{
                  background: "#FF2E8E",
                }}
              ></div>
              <ul>
                <li>
                  <span>Stacking token 20%:</span> Unlock 5% always, every 30
                  days unlock 5% more.
                </li>
              </ul>
            </ContentItem>
            <ContentItem>
              <div
                style={{
                  background: "#FF449A",
                }}
              ></div>
              <ul>
                <li>
                  <span>Development Fund 10%:</span>Unlock 10%, every 30 days
                  unlock10% more.
                </li>
              </ul>
            </ContentItem>
            <ContentItem>
              <div
                style={{
                  background: "#FF6AAE",
                }}
              ></div>
              <ul>
                <li>
                  <span>Company 15%: </span> Unlock 10% always, open 5% every 1
                  month.
                </li>
              </ul>
            </ContentItem>
            <ContentItem>
              <div
                style={{
                  background: "#FF89BF",
                }}
              ></div>
              <ul>
                <li>
                  <span>Strategic partnership 7%:</span> Unlock 30%, unlock 30%
                  after 6 months, unlock 40% after 1 year.
                </li>
              </ul>
            </ContentItem>
            <ContentItem>
              <div
                style={{
                  background: "#FFA5CE",
                }}
              ></div>
              <ul>
                <li>
                  <span>Marketing 15%:</span> Unlock always 20%, every 30 days
                  unlock 10% more.
                </li>
              </ul>
            </ContentItem>
          </div>
          <img src={ChartImg} alt="chart" />
        </Content>
      </Container>
    </Wrapper>
  );
}
