import React from "react";
import styled from "styled-components";
import Logo from "../../Assets/images/Logo.png";
import IconGit from "../../Assets/images/icon-git.png";
import IconTwit from "../../Assets/images/icon-twit.png";
import IconLinked from "../../Assets/images/icon-linked.png";
import IconMedium from "../../Assets/images/icon-medium.png";
import IconPython from "../../Assets/images/icon-python.png";
const Wrapper = styled.div`
  background: #171717;
  padding: 56px 27px;
  display: flex;
  flex-direction: column;
  align-items: center;
  p {
    font-family: Mulish;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: #e83e8c;
  }
`;
const Social = styled.div`
  padding: 36px 0px;
  display: flex;
  gap: 34px;
`;
export default function Footer() {
  return (
    <Wrapper>
      <img src={Logo} alt="Logo" />
      <Social>
        <img src={IconGit} alt="Git" />
        <img src={IconTwit} alt="Twit" />
        <img src={IconLinked} alt="Linked" />
        <img src={IconMedium} alt="Medium" />
        <img src={IconPython} alt="Python" />
      </Social>
      <p>The best decentralized payment and digital wallet ecosystem 2022.</p>
    </Wrapper>
  );
}
