import React from "react";
import styled from "styled-components";
import Container from "../../../Components/container";
import Blur3 from "../../../Assets/images/Blur3.png";
import Button from "../../../Components/button";
import IconUncheck from "../../../Assets/images/icon-uncheck.png";
const Wrapper = styled.div`
  padding: 45px 0 75px;
  background-image: url(${Blur3});
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  h1 {
    font-family: Roboto;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    text-transform: uppercase;
    color: #e83e8c;
    margin-bottom: 20px;
  }
  h2 {
    font-family: Mulish;
    font-weight: bold;
    font-size: 36px;
    line-height: 44px;
    text-align: center;
    color: #ffffff;
    margin-bottom: 54px;
  }
  h3 {
    font-family: Roboto;
    font-weight: 600;
    font-size: 24px;
    line-height: 47px;
    color: #f5f5f5;
    margin-bottom: 20px;
  }
  p {
    font-family: Roboto;
    font-size: 18px;
    line-height: 21px;
    color: #a6a6a6;
    margin-bottom: 5px;
  }
`;
const Flex = styled.div`
  display: flex;
`;
const Content = styled.div`
  width: 40%;
  margin-right: 70px;
  p {
    margin-bottom: 60px;
  }
`;
const Form = styled.div`
  width: 60%;
  p {
    margin-bottom: 5px;
  }
  textarea {
    width: 100%;
    background: #000000;
    border-radius: 7px;
    margin-bottom: 10px;
  }
  button {
    width: 100%;
  }
`;
const Checkbox = styled.div`
  display: flex;
  align-items: center;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  color: #ffffff;
  margin-bottom: 15px;
  span{
      margin-left: 5px;
      color: #e83e8c;
  }
  img{
    margin-right: 15px;
  }
`;
export default function Contact() {
  return (
    <Wrapper>
      <Container>
        <h1>Contact us</h1>
        <h2>Contact us</h2>
        <Flex>
          <Content>
            <h3>Ask a question</h3>
            <p>
              Please contact us. We are here to answer all your questions about
              our Centbox ecosystem
            </p>
            <h3>Mail us</h3>
            <p>info@centbox.io</p>
          </Content>
          <Form>
            <p>Enter yourname</p>
            <textarea></textarea>
            <p>Enter a valid email address</p>
            <textarea></textarea>
            <p>Enter your messenger</p>
            <textarea rows="10"></textarea>
            <Checkbox>
              <img src={IconUncheck} alt="icon" />I accept the <span> Terms of use</span>
            </Checkbox>
            <Button>Send Messenger</Button>
          </Form>
        </Flex>
      </Container>
    </Wrapper>
  );
}
