import React from "react";
import styled from "styled-components";
import Container from "../../../Components/container";
import Eco1 from "../../../Assets/images/eco-1.png";
import ArrowTop from "../../../Assets/images/arrow-top.png";
import ArrowBottom from "../../../Assets/images/arrow-bottom.png";
import ArrowLeft from "../../../Assets/images/arrow-left.png";
import ArrowRight from "../../../Assets/images/arrow-right.png";
import ArrowTopRight from "../../../Assets/images/arrow-topright.png";
import ArrowTopLeft from "../../../Assets/images/arrow-topleft.png";
import ArrowBottomRight from "../../../Assets/images/arrow-bottomright.png";
import ArrowBottomLeft from "../../../Assets/images/arrow-bottomleft.png";
const Wrapper = styled.div`
  padding: 100px;
  h2 {
    font-family: Roboto;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    text-transform: uppercase;
    color: #e83e8c;
    margin-bottom: 12px;
  }
  h1 {
    font-family: Mulish;
    font-weight: bold;
    font-size: 36px;
    line-height: 44px;
    text-align: center;
    color: #ffffff;
    margin-bottom: 20px;
  }
  p {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    color: #a6a6a6;
    margin-bottom: 56px;
  }
`;
const Content = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 800px;
  position: relative;
  .top {
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    img {
      top: calc(100% + 20px);
      left: 50%;
      transform: translateX(-50%);
    }
  }
  .bottom {
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    img {
      bottom: calc(100% + 20px);
      left: 50%;
      transform: translateX(-50%);
    }
  }
  .left {
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    img {
      top: 50%;
      left: calc(100% + 20px);
      transform: translateY(-50%);
    }
  }
  .right {
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    img {
      top: 50%;
      right: calc(100% + 20px);
      transform: translateY(-50%);
    }
  }
  .top-right {
    top: 15%;
    right: 15%;
    img {
      top: 100%;
      right: 100%;
    }
  }
  .top-left {
    top: 15%;
    left: 15%;
    img {
      top: 100%;
      left: 100%;
    }
  }
  .bottom-right {
    bottom: 15%;
    right: 15%;
    img {
      bottom: 100%;
      right: 100%;
    }
  }
  .bottom-left {
    bottom: 15%;
    left: 15%;
    img {
      bottom: 100%;
      left: 100%;
    }
  }
`;
const Center = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
`;
const Item = styled.div`
  max-width: 160px;
  height: 75px;
  display: flex;
  align-items: center;
  position: absolute;
  background: #272727;
  border-radius: 19px;
  padding: 0 40px;
  font-family: Roboto;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #ffffff;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  &:hover{
    background: #e83e8c;
  }
  img {
    position: absolute;
  }
`;
export default function Ecosystem() {
  return (
    <Wrapper>
      <Container>
        <h2>Ecosystem</h2>
        <h1>Centbox Expansion Ecosystem 2022</h1>
        <p>Our vision is the payment application for digital currency</p>

        <Content>
          <Center>
            <img src={Eco1} alt="Eco1" />
          </Center>
          <Item className="top">
            Payments
            <img src={ArrowTop} alt="ArrowTop" />
          </Item>
          <Item className="bottom">
            Storage 10000 coin 
            {` & `}token
            <img src={ArrowBottom} alt="ArrowBottom" />
          </Item>
          <Item className="left">
            Extention for Chorme
            <img src={ArrowLeft} alt="ArrowLeft" />
          </Item>
          <Item className="right">
            Swap Defi
            <img src={ArrowRight} alt="ArrowRight" />
          </Item>
          <Item className="top-right">
            Staking
            <img src={ArrowTopRight} alt="ArrowTopRight" />
          </Item>
          <Item className="top-left">
            App IOS {`&`}
            Android
            <img src={ArrowTopLeft} alt="ArrowTopLeft" />
          </Item>
          <Item className="bottom-right">
            Dapp Browser
            <img src={ArrowBottomRight} alt="ArrowBottomRight" />
          </Item>
          <Item className="bottom-left">
            NFT Market
            <img src={ArrowBottomLeft} alt="ArrowBottomLeft" />
          </Item>
        </Content>
      </Container>
    </Wrapper>
  );
}
