import React from "react";
import styled from "styled-components";
import Container from "../../../Components/container";
import Staking1 from "../../../Assets/images/staking-1.png";
import Staking2 from "../../../Assets/images/staking-2.png";
import Staking3 from "../../../Assets/images/staking-3.png";
import StakingArrow from "../../../Assets/images/staking-arrow.png";
const Wrapper = styled.div`
  background: #171717;
  padding: 75px 0 100px;
&>div{
    background: url(${Staking1}), url(${Staking2}), url(${Staking3});
    background-size: 200px 200px, 300px 300px, 300px 300px;
    background-position: left top, right center, 15% 110%;
    background-repeat: no-repeat;
}
  h2 {
    font-family: Roboto;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    text-transform: uppercase;
    color: #e83e8c;
    margin-bottom: 12px;
  }
  h1 {
    font-family: Mulish;
    font-weight: bold;
    font-size: 36px;
    line-height: 44px;
    text-align: center;
    color: #ffffff;
    margin-bottom: 20px;
  }
  p {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    color: #a6a6a6;
    margin-bottom: 56px;
  }
`;

const Content = styled.div`
  & > div {
    &:nth-child(1) {
      padding-left: 20px;
    }
    &:nth-child(2) {
      margin-left: 20%;
    }
    &:nth-child(3) {
      margin-left: 40%;
    }
    &:nth-child(4) {
      margin-left: 60%;
    }
  }
`;
const Step = styled.div`
  background: #272727;
  border-radius: 19px;
  padding: 40px 0;
  margin-bottom: 20px;
  text-align: center;
  font-family: Mulish;
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #ffffff;
  position: relative;
  width: 30%;
  transition: all 0.3s ease;
  img {
    position: absolute;
    top: calc(100% + 15px);
    left: 70px;
  }
  &:hover{
    cursor: pointer;
    background: #e83e8c;
  }
`;
export default function Staking() {
  return (
    <Wrapper>
      <Container>
        <h2>Staking</h2>
        <h1>
          Collect Near and Polkadot through <br />
          Multi Staking on Centpay
        </h1>
        <p>
          Staking Near {`&`} Polkadot on Centbox to build sustainable profits
        </p>
        <Content>
          <Step>
            Initialization
            <img src={StakingArrow} alt="arrow" />
          </Step>
          <Step>
            Hold CEN Token
            <img src={StakingArrow} alt="arrow" />
          </Step>
          <Step>
            Choose a plan
            <img src={StakingArrow} alt="arrow" />
          </Step>
          <Step>
            Get profit
          </Step>
        </Content>
      </Container>
    </Wrapper>
  );
}
