import React from "react";
import styled from "styled-components";
import Container from "../../../Components/container";
import Promo from "./promo";

import Blur2 from "../../../Assets/images/Blur2.png";
import Blur3 from "../../../Assets/images/Blur3.png";
import Blur4 from "../../../Assets/images/Blur4.png";
import Blur5 from "../../../Assets/images/Blur5.png";
import Products from "./products";
const Wrapper = styled.div`
  background: url(${Blur2}), url(${Blur3}), url(${Blur4}), url(${Blur5});
  background-size: 900px 900px, 900px 900px, 900px 900px, 900px 900px;
  background-repeat: no-repeat;
  background-position: top right, left center, right 70%, left 110%;
`;

export default function Main() {
  return (
    <Wrapper>
      <Container>
        <Promo />
        <Products />
      </Container>
    </Wrapper>
  );
}
