import React from "react";
import styled from "styled-components";
import IconCopy from "../../../Assets/images/icon-copy.png";
import Button from "../../../Components/button";
const Wrapper = styled.div`
  background-color: #171717;
  padding: 80px 0;
  h1 {
    font-family: Roboto;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    text-transform: uppercase;
    color: #e83e8c;
    margin-bottom: 20px;
  }
  h2 {
    font-family: Mulish;
    font-weight: bold;
    font-size: 36px;
    line-height: 44px;
    text-align: center;
    color: #ffffff;
    margin-bottom: 24px;
  }
  p {
    font-family: Roboto;
    font-size: 18px;
    line-height: 21px;
    color: #a6a6a6;
    text-align: center;
    margin-bottom: 5px;
  }
`;
const InputContainer = styled.div`
  width: 350px;
  margin: 40px auto 10px;
`;
const Input = styled.input`
  margin: 5px 0;
  width: 100%;
  padding: 12px;
  font-family: Roboto;
  font-size: 14px;
  line-height: 17px;
  color: #a6a6a6;
  background: #000000;
  border-radius: 7px;
  border: none;
  &::placeholder {
    color: #a6a6a6;
  }
`;
const Address = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;

  text-decoration-line: underline;
  color: #f969b2;
`;
const ButtonContainer = styled.div`
  display: flex;
  justify-content: center;
  margin-top: 15px;
  button {
    width: 350px;
  }
`;
export default function Sale() {
  return (
    <Wrapper>
      <h1>Sale</h1>
      <h2>Token Sale (Private Sale)</h2>
      <p>
        Join our private fundraising round to participate in Core Team Centbox
      </p>
      <InputContainer>
        <Input placeholder="Full Name" />
        <Input placeholder="Your Email" />
        <Input placeholder="ID Telegram" />
        <Input placeholder="Deposit Amount (BUSD)" />
      </InputContainer>
      <p>Centbox Wallet</p>
      <Address>
        0x60C2Ce7A7F2ac92be4934F53bA9DeE05f6a022E7{" "}
        <img src={IconCopy} alt="copy" />
      </Address>
      <ButtonContainer>
      <Button>Confirm</Button>
      </ButtonContainer>
    </Wrapper>
  );
}
