import React from "react";
import styled from "styled-components";
import Button from "../../../../Components/button";
import Product1 from "../../../../Assets/images/product-1.png";
import Product2 from "../../../../Assets/images/product-2.png";
import Product3 from "../../../../Assets/images/product-3.png";
import Product4 from "../../../../Assets/images/product-4.png";
import Step1 from "../../../../Assets/images/step-1.png";
import Step2 from "../../../../Assets/images/step-2.png";
import Step3 from "../../../../Assets/images/step-3.png";
const Wrapper = styled.div`
  padding-top: 30px;
  h2 {
    font-family: Roboto;
    font-weight: 500;
    font-size: 18px;
    line-height: 18px;
    color: #e83e8c;
    margin-bottom: 20px;
    text-transform: uppercase;
  }
  h1 {
    font-family: Mulish;
    font-weight: bold;
    font-size: 36px;
    line-height: 44px;
    color: #ffffff;
    margin-bottom: 20px;
  }
  p {
    font-family: Roboto;
    font-size: 16px;
    line-height: 19px;
    color: #a6a6a6;
    margin-bottom: 20px;
  }
  ul {
    margin-bottom: 20px;

    li {
      list-style: disc inside;
      font-family: Roboto;
      font-size: 16px;
      line-height: 19px;
      color: #a6a6a6;
    }
  }
`;
const Content = styled.div`
  display: flex;
  align-items: center;
  gap: 70px;
`;
const Grid = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0 50px;
`;
const Steps = styled.div`
  text-align: center;
  padding: 65px 0;
`;
const StepsContent = styled.div`
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
`;
const StepsItem = styled.div`
  font-family: Roboto;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  color: #a6a6a6;
  img {
    transition: all 0.3s ease;
    max-width: 255px;
    cursor: pointer;
  }
  h3 {
    transition: all 0.3s ease;

    width: 70%;
    margin: 0 auto;
  }
  &:hover {
    img {
      transform: scale(1.1);
    }
    h3 {
      transform: translateY(10px);
    }
  }
`;
export default function Products() {
  return (
    <Wrapper>
      <Content>
        <div>
          <h2>utilities</h2>
          <h1>Cryptocurrency payment via Centpay Wallet</h1>
          <p>
            Cryptocurrency payment methods are more and more popular and
            developed. Many businesses are starting to accept payment with
            cryptocurrencies. Centpay Wallet also offers API payment for our
            partners.
          </p>
          <Button>Download Now</Button>
        </div>
        <img src={Product1} alt="product-1" />
      </Content>
      <Content>
        <img src={Product2} alt="product-2" />
        <div>
          <h2>multifunctional</h2>
          <h1>Centpay Wallet security</h1>
          <p>
            Centpay Wallet is a multifunctional wallet combining security and
            utilities. Security structure includes passphrases, 12 security
            phrases, 2FA. Besides, the user's public and private keys are not
            stored on any centralized server. Both users and assets data are
            encrypted. Hence, it is difficult for hackers and scammers to attack
            digital assets from our customers.
          </p>
          <Button>Download Now</Button>
        </div>
      </Content>

      <Steps>
        <h2>Just follow these 3 steps</h2>
        <h1>Easy to start</h1>
        <StepsContent>
          <StepsItem>
            <img src={Step1} alt="step-1" />
            <h3>Register on Website or Download Centbox</h3>
          </StepsItem>
          <StepsItem>
            <img src={Step2} alt="step-2" />
            <h3>Create your wallet and save passphrase</h3>
          </StepsItem>
          <StepsItem>
            <img src={Step3} alt="step-3" />
            <h3>Start exchanging your cryptocurrencies</h3>
          </StepsItem>
        </StepsContent>
        <Button>Download Now</Button>
      </Steps>
      <Content>
        <div>
          <h2>utilities</h2>
          <h1>100+ cryptocurencies stored in Centpay wallet</h1>
          <Grid>
            <ul>
              <li>Bitcoin</li>
              <li>Ethereum</li>
              <li>Binance</li>
              <li>Solana</li>
              <li>Cardano</li>
            </ul>
            <ul>
              <li>Xrp</li>
              <li>Polkadot</li>
              <li>Dogecoin</li>
              <li>Near Protocol</li>
              <li>Avalanche</li>
            </ul>
          </Grid>
          <Button>Read more</Button>
        </div>
        <img src={Product3} alt="product-3" />
      </Content>
      <Content>
        <img src={Product4} alt="product-4" />
        <div>
          <h2>multifunctional</h2>
          <h1>NFT assets in the Metaverse universe</h1>
          <Grid>
            <ul>
              <li>Axie Infinity</li>
              <li>Decentraland</li>
              <li>Mobox</li>
            </ul>
            <ul>
              <li>Fortine</li>
              <li>ThetanArena</li>
              <li>Sanbox</li>
            </ul>
          </Grid>
          <Button>Download Now</Button>
        </div>
      </Content>
    </Wrapper>
  );
}
