import React from "react";
import styled from "styled-components";
import IconList from "../../../../Assets/images/icon-list.png";
const Wrapper = styled.div`
  background: #171717;
  padding: 40px 100px 70px 100px;
  h2 {
    font-family: Mulish;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    color: #e83e8c;
    margin-bottom: 16px;
  }
  h1 {
    font-family: Mulish;
    font-style: normal;
    font-weight: bold;
    font-size: 36px;
    line-height: 44px;
    text-align: center;
    color: #ffffff;
    margin-bottom: 48px;
  }
`;
const Grid = styled.div`
  display: grid;
  grid-template: repeat(3, 1fr) / 1fr 1fr;
  gap: 0 80px;
`;
const GridItem = styled.div`
  padding: 20px;
  display: flex;
  gap: 15px;
  font-family: Mulish;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
`;
export default function Promo() {
  return (
    <Wrapper>
      <h2>Promo</h2>
      <h1>
        Protect your assets with <br /> Centpay Wallet
      </h1>
      <Grid>
        <GridItem>
          <img src={IconList} alt="icon" />A fully featured digital wallet
        </GridItem>
        <GridItem>
          <img src={IconList} alt="icon" />
          Staking directly on the wallet app
        </GridItem>
        <GridItem>
          <img src={IconList} alt="icon" />
          Store NFT assets of any platform
        </GridItem>
        <GridItem>
          <img src={IconList} alt="icon" />
          Private key is stored decentralized
        </GridItem>
        <GridItem>
          <img src={IconList} alt="icon" />
          Over 100+ digital assets in circulation
        </GridItem>
        <GridItem>
          <img src={IconList} alt="icon" />
          Near Protocol and Polkadot Partners
        </GridItem>
      </Grid>
    </Wrapper>
  );
}
